.ModalBackground {
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.25);
}

.Modal {
  padding: 16px;
  background: #fff;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  border-radius: 33px;
}

.PageListItem {
  display: inline-block;
  width: 100%;
  margin: 4px 0;
}

.ItemButtonActive {
  background-color: var(--primary);
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.2),
    0px 0px 0px 0px rgba(0, 0, 0, 0.14), 0px 1px 2px -2px rgba(0, 0, 0, 0.12);
}

button:disabled.ItemButtonActive {
  color: white;
}

.PageList {
  padding: 0;
}

.App {
  text-align: center;
}

.Container {
  display: flex;
  justify-content: center;
}

.Container > * {
  flex: 0 1;
  margin: 0 16px;
}

.Form {
  display: flex;
  flex-direction: column;
}

:root {
  --primary: #00cdac;
  --secondary: #ff6700;
  --gray: #666666;
}

* {
  outline: 0;
  color: #666666;
  color: var(--gray);
}

html,
body {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

body {
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

canvas {
  border: 2px solid #00cdac;
  border: 2px solid var(--primary);
  border-radius: 33px;
  background-color: #fff;
}

h1 {
  text-align: center;
  color: #00cdac;
  color: var(--primary);
}

button {
  font-size: 0.75em;
  padding: 6px 12px;
  width: 100%;
  white-space: nowrap;
  background-color: #fff;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  border: 2px solid #00cdac;
  border: 2px solid var(--primary);
  border-radius: 13.5px;
  color: #00cdac;
  color: var(--primary);
}

button:not(:disabled):hover {
  cursor: pointer;
  -webkit-filter: brightness(105%);
          filter: brightness(105%);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.2),
    1px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 2px 0px rgba(0, 0, 0, 0.12);
}

button:active,
button:focus {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

button.large {
  font-size: 1.125em;
  padding: 8px 16px;
  border-radius: 18.5px;
}

button.primary {
  color: #fff;
  background-color: #00cdac;
  background-color: var(--primary);
  border: none;
}

button:disabled {
  color: #666666;
  color: var(--gray);
}

label {
  line-height: 150%;
}

input {
  padding: 4px 8px;
  font-size: 1.25em;
  margin-bottom: 16px;
  border: 2px solid #666666;
  border: 2px solid var(--gray);
  border-radius: 13.5px;
  color: #666666;
  color: var(--gray);
}

input:active,
input:focus {
  border-color: #00cdac;
  border-color: var(--primary);
  color: #00cdac;
  color: var(--primary);
}

label {
  margin-bottom: 2px;
}

